	module.exports = {"data":{"post":{"html":"<p>Thanks to the undue influence of American programs in German television, Christopher is insisting that we go trick-or-treating tomorrow night. Super RTL has been running Halloween specials of all their cartoons yesterday and today. He wants to dress as a ghost and collect a sack load of candy. And carve a pumpkin.</p>\n<p>I try to tell him that Halloween is an American holiday, and that German households will not be stocking loads of treats, particularly in the rural area where we live, but he says \"You're American, Papa. You know how it works.\"</p>\n<p>This is despite the fact we have 3 lantern marches (local town, old kindergarten, new school class) planned within one week starting on Saturday. Of course, lantern marches only involve open flames and songs vaguely related to astrology, which is nowhere near as exciting as collecting sackloads of candy in while dressed in gruesome costumes. Germans do that stuff for Karnival in late winter... well, at least in Cologne and Düsseldorf.</p>","fields":{"title":"The Bastardization of German Culture","date":"30 Oct 2006","url":"https://papascott.de/2006-10-30-the-bastardization-of-german-culture/"}}},"pathContext":{"slug":"/2006-10-30-the-bastardization-of-german-culture/","prev":{"fields":{"title":"NPR Berlin","date":"28 Oct 2006","slug":"/2006-10-28-npr-berlin/"}},"next":{"fields":{"title":"Expat Pitfalls","date":"31 Oct 2006","slug":"/2006-10-31-expat-pitfalls/"}}}}

